<template>
    <div>
            <van-nav-bar
            title="榜单"
            left-text="返回" 
            @click-left="toback"
            :fixed="true"
            />
            <div class="box-class clearfix">
                <div class="class-one fl" v-for="(item,index) in titleData" :key="index" :class="{active:item.isActive}" @click="toggleCl(index)">{{item.title}}</div>
                
            </div>
            <div class="last-list clearfix" >
                <div class="list-one clearfix fl"  @click="goTopdetail(item._id,item.shortTitle)" v-for="(item,index) in lastData" :key="index" >
                   
                   <div class="one-center fl ">{{item.shortTitle}}</div>
                   <div class="one-right fr"><img src="../assets/images/big.png" alt="" class="auto-img"></div>
                </div>
                
            </div>
    </div>
</template>

<script>
    export default {
        data(){
            return{
                titleData:[
                   
                    {
                        title:"epub",
                        isActive:true
                    },
                     {
                        title:"女生",
                        isActive:false
                    },
                    {
                        title:"男生",
                        isActive:false
                    },
                    {
                        title:"出版",
                        isActive:false
                    },
                    
                ],
                topData:[],
                lastData:[]
            }
            
        },
        created(){
            this.getTop()
            
        },
        methods:{
            toggleCl(index){
                for(let i = 0 ; i < this.titleData.length ; i ++){
                    this.titleData[i].isActive = false ;
                }
                this.titleData[index].isActive = true
                this.allData(index)
            },
            toback(){
                this.$router.go(-1)
            },
            
            allData(index){
                switch(index){
                    case 1 :
                        this.lastData = this.topData.female;
                        break;
                    case 2 :
                        this.lastData = this.topData.male;
                        break;
                    case 3 :
                        this.lastData = this.topData.picture;
                        break;
                    case 0 :
                        this.lastData = this.topData.epub;
                        break;
                }
                
            },
            imgKey(value) {
                return unescape(value).slice(7);
            },
            getTop(){
                this.$toast.loading({
                        message: "加载中...",
                        forbidClick: true,
                        duration: 0
                    });
                    this.axios({
                        method: "GET",
                        url: "/rank-category",
                        
                    }).then(result => {
                        this.$toast.clear(); 
                        this.topData = result.data
                        this.allData(0)
                    })
                    .catch(err => {
                        this.$toast.clear();
                        
                    });
            },
            
            goTopdetail(_id,title){
                this.$router.push({name:'Topdetail',query:{_id,title}})
            },
            
            
        }
    }
</script>

<style lang="less" scoped>
/deep/.van-nav-bar__title{
    font-weight: 700;
    color: #78797a;
}
/deep/.van-nav-bar__text{
    color: #575759;
}
.box-class{
    margin-top: 46px;
    background-color: #fff;
    padding-bottom: 20px;
    padding-top: 10px;
    border-bottom: 1px solid #bfbfbf;
    .class-one{
        
        // font-size: 14px;
        font-weight:700;
        margin-left: 34px;
        margin-right:34px ;
        padding:15px 0;
        color: #78797a;
        &.active{
            border-bottom: 3px solid #1cc993;
            color:#1cc993 ;
        }
    }
}
.last-list{
    
    .list-one{
        background-color: #fff;
        width: 100%;
        box-sizing: border-box;
        border-bottom:1px solid #bfbfbf ;
        
    }
    .one-left{
        width: 100px;
        
    }
    .one-center{
        margin-left: 20px;
        margin-top: 17px;
        font-size: 16px;
        color: #464648;
    }
    .one-right{
        width: 30px;
        margin-top: 12px;
        margin-bottom: 13px;
        margin-right: 10px;
    }
}
</style>